function renderMobileGallery(mobileGallery, root) {
  const extras = {
    isHandheld: true
  };

  return renderComponent(mobileGallery, root, 'mobile-gallery-component', null, null, extras);
}

function renderMenu(menu, root, extras) {
  const menuContainerStyle = {
    position: 'relative'
  };

  const menuStyle = {
    marginTop: '-20px'
  };

  return renderComponent(menu, root, 'menu-component', menuContainerStyle, menuStyle, extras);
}

function renderComponent(properties, root, key, containerStyle, componentStyle, extras) {
  const {configShow} = root.state;
  const configObjectStyles = {
    display: configShow[key] ? 'block' : 'none'
  };

  const onClick = () => {
    configShow[key] = !configShow[key];
    root.setState({configShow: configShow});
  };

  return (<div key={key} style={containerStyle ? containerStyle : {}}>
    <h2>{'Setup for ' + properties.content_type}</h2>
    <div style={componentStyle ? componentStyle : {}}>
      <EntryFactory module={properties} extras={extras} />
    </div>
    <h4
      style={{cursor: 'pointer'}}
      onClick={onClick}>
      {(configShow[key] ? 'Hide' : 'Show') + ' config object'}
    </h4>
    <pre style={configObjectStyles}>{JSON.stringify(properties, undefined, 2)}</pre>
  </div>);
}

