import React from 'react';
import './style.scss';

/**
 * Progress Step Bar Component.
 */
export default class ProgressStepBar extends React.Component {
  render() {
    const steps = this.props.steps.map((value, index) => {
      const step = index + 1;
      return this.renderStep(value, step, step == this.props.currentStep);
    });

    return (
      <div className="progress-step-bar">
        <ul>{steps}</ul>
      </div>
    );
  }

  /**
   * Render particular step.
   *
   * @param {string} value text to show
   * @param {number|string} stepNumber step to show
   * @param {boolean} isActive is step active
   * @returns {object}
   */
  renderStep(value, stepNumber, isActive) {
    return (
      <li className={isActive ? 'progress-step-bar__active' : ''} key={stepNumber}>
        <span className="progress-step-bar__step">{stepNumber}</span>
        <span className="progress-step-bar__title">{value}</span>
      </li>
    );
  }
}

ProgressStepBar.propTypes = {
  steps: React.PropTypes.array,
  currentStep: React.PropTypes.number
};

ProgressStepBar.defaultProps = {
  steps: [],
  currentStep: 0
};



